import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Lightbox } from 'ngx-lightbox';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { share } from 'rxjs/operators';
import { CONFIG } from '../config';

require("./custom/js/scrollspy.min.js");
require("./custom/js/counter.init.js");
require("./custom/js/app.js");


@Component({
  selector: 'ti-main-site',
  templateUrl: './main_site.component.html',
  styleUrls: ['./main_site.component.scss']
})
export class TIMainSiteComponent implements OnInit {
  config: typeof CONFIG = CONFIG;
  activeFragment = this.route.fragment.pipe(share()); 
  constructor(
    private _lightbox: Lightbox,
    public route: ActivatedRoute
  ) {
    localStorage.setItem("theme","default");
    localStorage.removeItem("themeConfig");
   }

  testimonialOptions: OwlOptions = {
    loop: true,
    dots: true,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 1
      },
      940: {
        items: 1
      }
    },
    autoplay: true,
    autoplaySpeed:2000,
    autoplayHoverPause: true
  }

  screenshotsOptions: OwlOptions = {
    loop: true,
    dots: false,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 3
      },
      940: {
        items: 3
      }
    },
    center:true,
    startPosition: 0,
    // autoplay: true,
    // autoplaySpeed:2000,
    // autoplayHoverPause: true,
    nav    : true,
    smartSpeed :900,
    navText : ['<span class="nav-btns material-icons-outlined">arrow_back</span>','<span class="nav-btns material-icons-outlined">arrow_forward</span>']
  }
  
  ngOnInit() {
    
  }

  onViewImage(image: any) {
    const album:any = [
      {
        src:image
      }
    ]
    this._lightbox.open(album, 0)
  }

}
