import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TIMainSiteComponent } from './main_site.component';

const routes: Routes = [
  { path: '', component: TIMainSiteComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TIMainSiteRoutingModule { }
